<template>
    <div class="liui-page">
      <header class="liui-bar liui-pos-f">
        <div class="icon iconfont icon-fanhuitubiao" @click="gotoHome"></div>
        <h1 class="title">智能语音实验室</h1>
      </header>
      <section class="liui-content">
        <div class="login-reg-page liui-pos-f">
          <img class="bg" src="../assets/images/login-register-bg.png">
          <div class="login-reg-body">
            <div class="login-reg-header">
              <!-- <p>注册 & 登录</p> -->
              <h2>注册&nbsp;•&nbsp;我的智能语音实验室</h2>
            </div>
            <div class="login-reg-main">
              <div class="login-reg-item">
                <input @input="checkPhone" placeholder="请输入手机号" type="number" v-model="phone">
              </div>
              <div class="login-reg-item">
                <input  @input="checkCaptcha" placeholder="请输入验证密码" type="text" v-model="captcha">
                <span class="r-btn" @click="getCaptcha">{{countTxt}}</span>
              </div>
              <div class="login-reg-item">
                <input  @input="checkPassword" placeholder="请输入密码" type="password" v-model="password">
              </div>
              <div class="login-reg-item login-reg-item-btn">
                <button @click="RegisterComfirm">注册</button>
              </div>
              <div class="login-reg-item service">
                <label><input class="checkbox" type="checkbox" checked/><span></span>同意《智能语音实验室用户服务协议》</label>
              </div>
            </div>
          </div>
        </div>
      </section>
    </div>
</template>

<script>
export default {
  data () {
    return {
      phone: '',
      captcha: '',
      password: '',
      countTxt: '获取验证码',
      countDown: 60,
      isCount: false
    }
  },
  created(){
    let phone = this.$cookie.get("phone");
    if(phone){
      this.$router.push('/');
    }
  },
  methods:{
    gotoHome(){
      this.$router.push('/');
    },
    checkPhone(){
      if(this.phone.length > 11){
        this.phone = this.phone.substr(0,11);
      }
    },
    checkCaptcha(){
      if(this.captcha.length > 4){
        this.captcha = this.captcha.substr(0,4);
      }
    },
    checkPassword(){

    },
    getCaptcha(){
      if(this.phone != '' && this.phone.length == 11){
        if(!this.isCount){
          this.isCount = !this.isCount;
          //this.$toast.center('发送中...');
          this.$http('sms/send',{"mobile": this.phone,"event": 'register'}).then( res => {
            if(res.data.code == 1){
              this.$toast.center('发送验证码成功，注意查收~');
              this.countTxt = this.countDown + 's后重新发送';
              let clock = window.setInterval(() => {
                this.countDown--;
                this.countTxt = this.countDown + 's后重新发送'
                if (this.countDown < 0) {
                  window.clearInterval(clock)
                  this.countTxt = '发送验证码'
                  this.totalTime = 30
                  this.isCount = false;
                }
              },1000);
            }else{
              this.$toast.center(res.data.msg);
            }
          }).catch( err => {
            console.log(err);
          });
        }
      }else{
        this.$toast.center('请输入正确的手机号码');
      }
    },
    RegisterComfirm(){
      let _this = this;
      if(_this.phone != '' && _this.phone.length == 11){
        if(_this.captcha != '' && _this.captcha.length == 4){
          if(_this.password != '' && _this.password.length >= 6){
            _this.$loading('注册中...');
            _this.$http('user/register',{"mobile": _this.phone,"captcha":_this.captcha,"password": _this.password}).then( res => {
              _this.$loading.close();
              if(res.data.code == 1){
                _this.$router.push('/login');
                _this.$toast.center('注册成功');
              }else{
                _this.$toast.center(res.data.msg);
              }
            }).catch( err => {
              console.log(err);
            });
          }else{
            _this.$toast.center('请输入不少于6位数的密码');
          }
        }else{
          _this.$toast.center('请输入4位验证码');
        }
      }else{
        _this.$toast.center('请输入正确的手机号码');
      }
    }
  }
}
</script>

<style scoped>
.login-reg-page{
    top: 50px;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 2;
    background-color: #333;
}
.login-reg-item .icon-clear{
    position: absolute;
    top: 0;
    right: 30px;
    width: 30px;
    height: 50px;
    background: url(../assets/images/clear_icon.png) 9px 19px no-repeat;
    background-size: 12px 12px;
}
.login-reg-item .icon-change{
    position: absolute;
    top: 0;
    right: 0;
    width: 30px;
    height: 50px;
    background: url(../assets/images/login-register-pw-hide.png) 5px 19px no-repeat;
    background-size: 20px 12px;
}
.login-reg-item .icon-change.icon2{
    background: url(../assets/images/login-register-pw-show.png) 5px 19px no-repeat;
    background-size: 20px 12px;
}
.login-reg-item span.r-btn{
  position: absolute;
  top: 0;
  right: 0;
  line-height: 50px;
  color: #ddd;
  font-size: 1rem;
}
.login-reg-item .checkbox{
  width: 15px;
  height: 15px;
  border: 0; 
  opacity: 0;
  display: none;
}
.login-reg-item label .checkbox+span{
  display: inline-block;
  width: 15px;
  height: 15px;
  border-radius: 2px;
  margin: 0 5px -3px 0;
  overflow: hidden;
  background: url(../assets/images/icon_checkbox.png) 0 -25px no-repeat;
  background-size: 15px 40px;
}
.login-reg-item label .checkbox:checked+span{
  background: url(../assets/images/icon_checkbox.png) 0 0 no-repeat;
  background-size: 15px 40px;
}
.service{
  height: 15px;
  padding-top: .5rem;
  line-height: 15px;
  color: #999;
  font-size: 12px;
}
</style>



