<template>
    <div class="liui-page">
      <header class="liui-bar liui-pos-f">
        <div class="icon iconfont icon-fanhuitubiao" @click="gotoHome"></div>
        <h1 class="title">个人中心</h1>
        <div class="liu-bar-r-txt"><router-link to="/security"><i class="iconfont icon-shezhi"></i></router-link></div>
      </header>
      <section class="liui-content">
        <div class="user-page">
          <div class="user-page-header">
            <div class="info" @click="gotoUserEdit">
              <div class="protype">
                <p><label>{{nickname}}</label><span class="iconfont icon-bianji"></span><p>
                <div class="vip">
                  <span :class="{ vip: isVip == 1 }">&nbsp;</span>
                </div>
              </div>
              <div class="img"><img :src="avatar"></div>
              
            </div>
          </div>
          <div class="user-page-body">
            <ul>
              <li>
                <router-link to="/record">
                  <span class="iconfont icon-biaoqianlan-yuyinhecheng"></span><label>合成记录</label><i class="iconfont icon-fanhuitubiao"></i>
                </router-link>
                </li>
            </ul>
          </div>
        </div>
      </section>
      <footer class="user-page-footer">
        <button @click="loginOut">退出登录</button>
      </footer>
    </div>
</template>

<script>
export default {
  data () {
    return {
      nickname: '',
      avatar: '',
      isVip: ''
    }
  },
  created(){
    let phone = this.$cookie.get("phone");
    if(phone){
      this.nickname = this.$cookie.get("nickname");
      this.avatar = this.$cookie.get("avatar");
      this.isVip = parseInt(this.$cookie.get("isvip"));
    }else{
      this.$router.push('/login');
    }
  },
  methods:{
    gotoHome(){
      this.$router.push('/');
    },
    gotoUserEdit(){
      this.$router.push('/edit');
    },
    loginOut(){
      this.$cookie.delete("phone");
      this.$cookie.delete("avatar");
      this.$cookie.delete("nickname");
      this.$cookie.delete("isvip");
      this.$cookie.delete("token");
      this.$cookie.delete("defaultSpeed");
      this.$cookie.delete("defaultVolume");
      this.$toast.center('退出成功');
      this.$router.push('/');
    }
  }
}
</script>

<style scoped>
body, html{
  background-color: #F3F4F5;
}
.user-page-header{
  position: relative;
  padding: 0.8rem;
  background-color: #F3F4F5;
}
.user-page-header .info{
  position: relative;
  z-index: 5;
  display: flex;
  padding: 2rem 1rem;
  border-radius: 5px;
  color: #fff;
  background-color: #5E5D5D;
  align-items: center;
}
.user-page-header .info .protype{
  position: relative;
  flex: 1;
  max-width: calc(100% - 60px);
  line-height: 2.5rem;
  font-size: 18px;
}
.user-page-header .info .protype p{
  display: flex;
  align-items: center;
}
.user-page-header .info .protype label{
  display: inline-block;
  max-width: 90%;
  overflow: hidden;white-space: nowrap;text-overflow: ellipsis;
}
.user-page-header .info .protype .iconfont{
  margin-left: 10px;
}
.user-page-header .info .vip span{
  display: inline-block;
  width: 80px;
  height: 22px;
  background: url(../../assets/images/vip.png) 0 0 no-repeat;
  background-size: 80px 44px;
}
.user-page-header .info .vip span.vip{
  background: url(../../assets/images/vip.png) 0 -22px no-repeat;
  background-size: 80px 44px;
}
.user-page-header .info .img img{
  width: 53px;
  height: 53px;
  margin-left: 5px;
  border-radius: 50%;
}
.user-page-body ul li a{
  display: flex;
  padding: 1.5rem 1rem;
  border-bottom: 1px solid #eee;
  color: #333;
  line-height: 1rem;
  background-color: #fff;
}
.user-page-body ul li a span{
  display: inline-block;
  margin-right: 5px;
  font-size: 20px;
  font-weight: bold;
}
.user-page-body ul li a label{
  flex: 1;
}
.user-page-body ul li a i{
  display: inline-block;
  width: 20px;
  transform: rotate(180deg);
  font-size: 0.8rem;
}
.user-page-body ul li + li{
  border-top:1px solid #eee;
}
.user-page-footer{
  position: fixed;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 99;
  padding: 1rem;
}
.user-page-footer button{
  display: block;
  width: 100%;
  height: 3.5rem;
  border-radius: 5px;
  border:0;
  background-color: #5E5D5D;
  color: #fff;
  font-size: 16px;
}
</style>



