<template>
  <div class="liui-page">
    <div class="tts-page1">
      <header class="liui-bar liui-pos-f">
        <div class="icon iconfont icon-fanhuitubiao" @click="gotoHome"></div>
        <h1 class="title">语音合成</h1>
      </header>
      <section class="liui-content">
        <div class="tts-page">
          <div class="tts-page-header">
            <!-- <p>语音合成</p> -->
            <h2>你是这样的“声音”</h2>
          </div>
          <div class="tts-page-body">
            <div class="tts-input" :class="{ animate: status }">
              <img class="bg" src="../../assets/images/tts-hc-bg.png">
              <div class="guide-cont">
                <img class="img1" src="../../assets/images/tts-hc-step1.png">
                <img class="img2" src="../../assets/images/tts-hc-step2.png">
              </div>
              <div class="txt-cont">
                <textarea v-bind:maxlength="totalWord" @input="descArea"  v-model="content" placeholder="点击输入合成内容" @focus.prevent="contentBlur"></textarea>
                <img v-show="isShowTextImg" src="../../assets/images/tts-hc-input-bg.png">
                <div v-show="content.length != 0" class="word">{{currWordLength}}&nbsp;/&nbsp;{{totalWord}}<i class="iconfont icon-qingchumima" @click="clearContent"></i></div>
                <div class="link-to" v-show="isvip == 1 && isComposite && content.length != 0"><router-link to="/record"><i class="iconfont icon-biaoqianlan-xiazai"></i>下载</router-link></div>
              </div>
              <button @click="contral_star" class="tts-btn" :class="{ active:isComposite && content.length != 0 }"></button>
              <audio ref="audioPlayer" src="https://bms.sounddat.heard-gl.com/assets/audio/mp3/shiting_1.0x.mp3" @ended="playEnded()" id="audioPlayer" controls></audio>
            </div>

            <div class="tts-option">
              <div class="cell" @click="ttsOption(0)">
                语音语速
                <p>{{currRate}}X</p>
              </div>
              <div class="cell" @click="ttsOption(1)">
                选择发音
                <p>{{currCategoryItemName}}</p>
              </div>
              <div class="cell" @click="ttsOption(2)">
                语音语量
                <p>{{currVolume * 100 }}%</p>
              </div>
            </div>
          </div>
        </div>
      </section>
      <footer class="fixed-bottom-nav">
        <ul>
          <li class="item active">
            <router-link to="/tts">
              <div class="iconfont icon-biaoqianlan-yuyinhecheng1"></div>
              <span>语音合成</span>
            </router-link>
          </li>
          <li class="item">
            <router-link to="/compare">
              <div class="iconfont icon-biaoqianlan-TTSduibi1"></div>
              <span>TTS对比</span>
            </router-link>
          </li>
          <li class="item">
            <router-link to="/theme">
              <div class="iconfont icon-biaoqianlan-zhutizhanshi1"></div>
              <span>主题展示</span>
            </router-link>
          </li>
        </ul>
      </footer>
    </div>
    <div class="tts-page2 liui-pos-f0" v-bind:class="{ opening: isActive }">
      <header class="liui-bar liui-pos-f">
        <div class="icon iconfont icon-fanhuitubiao" @click="closeOption"></div>
        <h1 class="title">合成设置</h1>
      </header>
      <section class="liui-content">
        <section class="option-page">
          <nav class="option-header-nav">
            <div class="item" v-for="(item,index) in optionTxt" :class="{ active: index == currOptionIndex }" @click="toggleOption(index)">
              <span>{{item.text}}</span>
            </div>
            <div class="line" ref="option_header_nav_line"></div>
          </nav>
          <div class="option-page-body">
            <div class="option-detail" :class="{ active: currOptionIndex == 0}">
              <div class="rate-cont">
                <div class="line"></div>
                <ul>
                  <li v-for="(item,index) in rateList" @click="rateChoose(item.value,index)" class="item" :class="{ active: item.value == currRateValue }">
                    <div class="dot"></div>
                    <span>{{item.text}}</span>
                  </li>
                </ul>
              </div>
              <div class="rate-cont-btn">
                <button @click="listening">试听</button>
                <button @click="setDefaultRate" :class="{ btn2: currRate == currRateValue }">设为默认</button>
              </div>
              <audio ref="listeningPlayer" src="" controls></audio>
            </div>
            <div class="option-detail" :class="{ active: currOptionIndex == 1}">
              <div class="option-category-menu">
                <ul>
                  <li v-for="(item,index) in CategoryList" @click="chooseCategory(item.id)" :class="{ active: item.id == currCategoryId }">
                    <i class="iconfont icon-biaoqianlan-yuyinhecheng"></i>{{item.name}}
                  </li>
                </ul>
              </div>
              <div class="option-category-list">
                <div class="c-item" v-for="(item,index) in categoryData" @click="chooseCategoryItem(item)" :class="{ active: item.id == currCategoryItemId }">
                  <div class="cell">
                    <div class="img">
                      <img :src="item.image_domain">
                      <label v-if="item.is_new == 1">新</label>
                    </div>
                    <div class="info">
                      <h3>{{item.name}}<i v-if="item.is_recommend == 1">V</i></h3>
                      <span>已有{{item.use_num}}人使用</span>
                    </div>
                  </div>
                  <div class="txt" v-if="item.description">
                    <p>
                      {{item.description}}
                      <i v-if="item.category_id == 29 && item.path_domain" class="icon" @click.stop="featureListening(item)"></i>
                    </p>
                  </div>
                </div>
              </div>
            </div>
            <div class="option-detail" :class="{ active: currOptionIndex == 2}">
              <!-- <div class="volume-cont">
                <el-slider
                  v-model="currVolume"
                  :step="0.1"
                  :min="0.2"
                  :max="2"
                  show-tooltip
                  show-stops
                  @change="changeVolume">
                </el-slider>
                <div class="txt">
                  <div class="txt-l">0.2</div>
                  <div class="txt-r">2</div>
                </div> 
                <button @click="setDefaultVolume" :class="{ active: currVolume == volumeVal }">设为默认</button>
              </div> -->
              
              <div class="rate-cont">
                <div class="line"></div>
                <ul>
                  <li v-for="(item,index) in volumeList" @click="changeVolume(item.value,index)" class="item" :class="{ active: item.value == currVolume }">
                    <div class="dot"></div>
                    <span>{{item.text}}</span>
                  </li>
                </ul>
              </div>
              <div class="rate-cont-btn small">
                <button @click="setDefaultVolume" :class="{ btn2: currVolume == volumeVal }">设为默认</button>
              </div>
            </div>
          </div>
          <div class="option-page-footer liui-pos-f">
            <button class="btn" @click="closeOption">完成</button>
          </div>
        </section>
      </section>
    </div>
    <!-- tts loading -->
    <div class="tts-loading-page liui-pos-f0" v-show="ttsLoading">
      <div class="layer"></div>
      <div class="tts-loading-body">
        <div class="tts-loading-main">
          <div class="loader-inner ball-clip-rotate">
            <div></div>
          </div>
          <div class="txt" id="ttsLoadingTXT">预计等待{{isCounting}}秒</div>
        </div>
        <div class="btn" @click="closeTTSLoading">取消</div>
      </div>
    </div>
    <!-- tts loading -->
    <div class="tts-loading-page liui-pos-f0" v-if="isPlaying">
      <div class="layer"></div>
      <div class="tts-loading-body">
        <div class="tts-loading-main">
          <div class="loader-inner line-scale-pulse-out-rapid">
            <div></div>
            <div></div>
            <div></div>
            <div></div>
            <div></div>
          </div>
          <div class="txt">试听中...</div>
        </div>
        <div class="btn" @click="closeTTSLoading">取消</div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      window_w: 0,
      isComposite: false,
      ttsLoading: false,//加载中
      isPlaying: false,//是否在播放
      isCounting: 20,//倒计时
      playSrc: '',
      status: false,
      isActive: false,
      content: '',
      currWordLength: 0,
      totalWord: parseInt(this.$cookie.get("isvip")) == 1 ? 200 : 200,
      //totalWord: parseInt(this.$cookie.get("isvip")) == 1 ? 200 : 50,
      isShowTextImg: window.localStorage.getItem("isShowTextImg") ? false : true,
      currOptionIndex: 0,
      optionTxt: [
        {
          text: '语音语速',
          value: 1
        },
        {
          text: '选择发音',
          value: 1
        },
        {
          text: '语音音量',
          value: 1
        }
      ],
      silderLeft: 0,
      CategoryList: [],
      currCategoryId: '',
      categoryData: [],
      currCategoryItemName: '',
      currCategoryItemId: 0,
      currRate: this.$cookie.get("defaultSpeed"),
      currListenSrc: '',
      listeningList:[
        {
          "id": 0,
          "src": "https://bms.sounddat.heard-gl.com/assets/audio/mp3/shiting_0.2x.mp3"
        },
        {
          "id": 1,
          "src": "https://bms.sounddat.heard-gl.com/assets/audio/mp3/shiting_0.5x.mp3"
        },
        {
          "id": 2,
          "src": "https://bms.sounddat.heard-gl.com/assets/audio/mp3/shiting_1.0x.mp3"
        },
        {
          "id": 3,
          "src": "https://bms.sounddat.heard-gl.com/assets/audio/mp3/shiting_1.5x.mp3"
        },
        {
          "id": 4,
          "src": "https://bms.sounddat.heard-gl.com/assets/audio/mp3/shiting_2.0x.mp3"
        },
      ],
      currRateValue: this.$cookie.get("defaultSpeed") || 1,
      rateList: [
        {
          text: '0.2X',
          value: 0.2
        },
        {
          text: '0.5X',
          value: 0.5
        },
        {
          text: '1X',
          value: 1.0
        },
        {
          text: '1.5X',
          value: 1.5
        },
        {
          text: '2X',
          value: 2.0
        }
      ],
      volumeList: [
        {
          text: '20%',
          value: 0.2
        },
        {
          text: '50%',
          value: 0.5
        },
        {
          text: '100%',
          value: 1.0
        },
        {
          text: '150%',
          value: 1.5
        },
        {
          text: '200%',
          value: 2.0
        }
      ],
      currVolume: parseFloat(this.$cookie.get("defaultVolume") || 1),
      volumeVal: parseFloat(this.$cookie.get("defaultVolume") || 1)
    }
  },
  created() {
    let phone = this.$cookie.get("phone");
    this.isvip = parseInt(this.$cookie.get("isvip"));
    if(!phone){
      this.$router.push('/login');
    }else{
      let _this = this;
      if ( this.timeOut ) {
        clearTimeout(this.timeOut);
      }
      this.hideGuide();
      this.$http('Category/index',{"type": "compound_cat","pid": "0"}).then( res => {
        let data = res.data;
        if(data.code == 1){
          _this.CategoryList = data.data;
          _this.currCategoryId = data.data[0].id;
          if(_this.currCategoryId){
            this.$http('Compoundcat/getList',{"category_id": _this.currCategoryId+"","page": "1","pagesize": "100"}).then( res => {
              let c_data = res.data;
              if(c_data.code == 1){
                _this.categoryData = c_data.data.data;
                _this.currCategoryItemName = c_data.data.data[0].name;
                _this.currCategoryItemId = c_data.data.data[0].id;
              }
            }).catch( err => {
              console.log(err);
            });
          }
        }
      }).catch( err => {
        console.log(err);
      });
    }
  },
  methods:{
    gotoHome(){
      this.$router.push('/');
    },
    hideGuide() {
      let _this = this;
      this.timeOut = setTimeout(() => {
        _this.status = !_this.status;
      }, 800);
    },
    //清空输入的文本
    clearContent(){
      this.content = '';
    },
    //敏感词检查
    descArea(){
      let textLength = this.content.length;
      // let pattern = new RegExp("[`~!@#$^&*()=|{}':;',\\[\\].<>/?~！@#￥……&*（）——|{}【】‘；：”“'。，、？]");
      // if(pattern.test(this.content)){
      //   this.content.replace(pattern, '');//禁止输入特殊字符
      //   console.log(typeof this.content);
      // }
      
      this.currWordLength = textLength;
      if(textLength > 0){
        this.$http('Alitextscan/contentFilter',{text:this.content}).then( res => {
          let data = res.data;
          if(data.code != 1){
            this.$toast.center(data.msg);
          }
        }).catch( err => {
          console.log(err);
        });
      }//else{
        //this.$toast.center('内容不能为空');
      //}
    },
    //获取光标
    contentBlur(){
      window.localStorage.setItem("isShowTextImg",'false');
      this.isShowTextImg = false;
    },
    //关闭配置页面
    closeOption(){
      this.isActive = false;
      this.$refs.listeningPlayer.pause();
      this.$refs.listeningPlayer.src = '';
    },
    //选择配置
    ttsOption(index){
      this.isActive = true;
      this.currOptionIndex = index;
      if(index == 0){
        this.$refs.option_header_nav_line.style.left = ((index+1)*this.window_w/4 - 24)+'px';
      }else if(index == 1){
        this.$refs.option_header_nav_line.style.left = ((index+1)*this.window_w/4 - 10)+'px';
      }else{
        this.$refs.option_header_nav_line.style.left = ((index+1)*this.window_w/4 + 4)+'px';
      }
    },
    //切换配置选项
    toggleOption(index){
      this.currOptionIndex = index;
      if(index == 0){
        this.$refs.option_header_nav_line.style.left = ((index+1)*this.window_w/4 - 24)+'px';
      }else if(index == 1){
        this.$refs.option_header_nav_line.style.left = ((index+1)*this.window_w/4 - 10)+'px';
      }else{
        this.$refs.option_header_nav_line.style.left = ((index+1)*this.window_w/4 + 4)+'px';
      }
    },
    //选择分类
    chooseCategory(id){
      let _this = this;
      _this.currCategoryId = id;
      _this.$http('Compoundcat/getList',{"category_id": _this.currCategoryId+"","page": "1","pagesize": "100"}).then( res => {
        let c_data = res.data;
        if(c_data.code == 1){
          _this.categoryData = c_data.data.data;
        }
      }).catch( err => {
        console.log(err);
      });
    },
    //选择类型
    chooseCategoryItem(item){
      this.currCategoryItemId = item.id;
      this.currCategoryItemName = item.name;
    },
    //选择语速
    rateChoose(value,index){
      //this.currRate = value;
      this.currRateValue = value;
    },
    //试听
    listening(){
      if(this.currRateValue == 0.2){
        this.currListenSrc = this.listeningList[0].src;
      }
      if(this.currRateValue == 0.5){
        this.currListenSrc = this.listeningList[1].src;
      }
      if(this.currRateValue == 1.0){
        this.currListenSrc = this.listeningList[2].src;
      }
      if(this.currRateValue == 1.5){
        this.currListenSrc = this.listeningList[3].src;
      }
      if(this.currRateValue == 2.0){
        this.currListenSrc = this.listeningList[4].src;
      }
      this.$refs.listeningPlayer.src = this.currListenSrc;
      this.$refs.listeningPlayer.play();
    },
    //试听特色
    featureListening(item){
      if(item.path_domain && item.path_domain.indexOf('.mp3') > -1){
        this.$refs.listeningPlayer.src = item.path_domain;
        this.$refs.listeningPlayer.play();
      }else{
        this.$toast.center('设置成功');
      }
    },
    //设为默认语速
    setDefaultRate(){
      // this.currRate = 1;
      // this.currRateIndex = 2;
      if(this.currRateValue != this.currRate){
        this.$http('user/setSpeed',{speed:this.currRateValue+''}).then( res => {
          let data = res.data;
          if(data.code == 1){
            this.currRate = this.currRateValue;
            this.$toast.center('设置成功');
            this.$cookie.set("defaultSpeed",this.currRate);
          }
        }).catch( err => {
          console.log(err);
        });
      }
    },
    //改变音量
    changeVolume(value,index){
      //console.log(this.currVolume);
      this.currVolume = value;
    },
    //设为默认音量
    setDefaultVolume(){
      if(this.currVolume != this.volumeVal){
        this.$http('user/setVolume',{volume:this.currVolume+''}).then( res => {
          let data = res.data;
          if(data.code == 1){
            this.volumeVal = this.currVolume;
            this.$cookie.set("defaultVolume",this.currVolume);
            this.$toast.center('设置成功');
          }
        }).catch( err => {
          console.log(err);
        });
      }
    },
    //合成播放完成
    playEnded(){
      this.isPlaying = false;
      //this.isComposite = false;
    },
    //关闭合成loading
    closeTTSLoading(){
      this.ttsLoading = false;
      this.isPlaying = false;
      this.$refs.audioPlayer.pause();
      this.$refs.audioPlayer.src = '';
    },

    //合成按钮
    contral_star(){
      let _this = this;
      let audioPlayerEl = _this.$refs.audioPlayer;

      // 兼容iphone不能播放的BUG
      audioPlayerEl.play();
      audioPlayerEl.pause();


      //let audioPlayerEl = document.getElementById('audioPlayer');
      let textValStorage = _this.localGet("textValStorage") == null ? '' : _this.localGet("textValStorage");
      let rateStorage = _this.localGet("rateStorage") == null ? '' : _this.localGet("rateStorage");
      let volumeStorage = _this.localGet("volumeStorage") == null ? '' : _this.localGet("volumeStorage");
      let cat_idStorage = _this.localGet("cat_idStorage") == null ? '' : _this.localGet("cat_idStorage");
      let audioStorage = _this.localGet("audioStorage") == null ? '' : _this.localGet("audioStorage");
      if(_this.content.length != 0){
        if(textValStorage == _this.content && parseFloat(rateStorage) == _this.currRate && parseFloat(volumeStorage) == _this.currVolume && parseInt(cat_idStorage) == _this.currCategoryItemId){//判断文本内容不改变、类别也未改变、语速未改变
          audioPlayerEl.src = audioStorage;
          audioPlayerEl.play();
          _this.isPlaying = true;
          _this.ttsLoading = false;
        }else{
          _this.ttsLoading = true;
          //先清除正计时，再开始正计时
          clearInterval(this._interval);

          //合成虚拟倒计时
          if(_this.content.length <= 30){
            _this.isCounting = 20;
          }else if(_this.content.length > 30 && _this.content.length <= 50){
            _this.isCounting = 40;
          }else if(_this.content.length > 50 && _this.content.length <= 100){
            _this.isCounting = 60;
          }else if(_this.content.length > 100 && _this.content.length <= 120){
            _this.isCounting = 70;
          }else if(_this.content.length > 120 && _this.content.length <= 150){
            _this.isCounting = 100;
          }else if(_this.content.length > 150 && _this.content.length <= 200){
            _this.isCounting = 120;
          }
          _this._interval = setInterval(() => {
            if(_this.isCounting != 0){
              _this.isCounting--;
            }else if(_this.isCounting == 0){
              _this.isCounting = 0;
              //_this.ttsLoading = false;
              _this.$toast.center('当前排队人数过多，请耐心等待');
              clearInterval(this._interval);
            }
          }, 1000);
          //脱敏处理
          _this.$http('Alitextscan/contentFilter',{text:_this.content}).then( res => {
            let filterData = res.data;
            if(filterData.code == 1){
                //请求参数
                let audioOption = {
                  "cat_id": _this.currCategoryItemId,
                  "type": "2",
                  "text": _this.content,
                  "speed": _this.currRate,
                  "volume": _this.currVolume,
                };
                _this.$http('Compound/mobileAdd',audioOption).then( res => {
                  //清除正计时
                  clearInterval(this._interval);
                  _this.isCounting = 0;

                  let audioData = res.data;
                  if(audioData.code == 1){
                    _this.playSrc = audioData.data.src_domain;
                    _this.localSet("textValStorage",_this.content);
                    _this.localSet("rateStorage",_this.currRate);
                    _this.localSet("volumeStorage",_this.currVolume);
                    _this.localSet("cat_idStorage",_this.currCategoryItemId);
                    _this.localSet("audioStorage",_this.playSrc);
                    if(_this.ttsLoading != false){
                      audioPlayerEl.src = audioData.data.src_domain;
                      audioPlayerEl.play();
                      _this.ttsLoading = false;
                      _this.isPlaying = true;
                      _this.isComposite = true;
                    }
                  }else{
                    _this.$toast.center(audioData.msg || '请输入正确的文本');
                    _this.ttsLoading = false;
                    _this.isPlaying = false;
                    _this.isComposite = false;
                  }
                }).catch( err => {
                  _this.isComposite = false;
                  console.log(err);
                });
            }else{
              _this.ttsLoading = false;
              _this.$toast.center(filterData.msg);
            }
          }).catch( err => {
            console.log(err);
          });
        }
      }else{
        _this.$toast.center('请先输入内容');
      }
    },
    localGet(k){
      return window.localStorage.getItem(k);
    },
    localSet(k,v){
      window.localStorage.setItem(k,v);
      return;
    }
  },
  mounted(){
    this.window_w = window.innerWidth || document.documentElement.clientWidth || document.body.clientWidth;
  },
  watch:{
    content(curVal){
      let textValStorage = this.localGet("textValStorage") == null ? '' : this.localGet("textValStorage");
      this.$nextTick(() => {
        this.content = curVal.replace(/[\"\'\<\>\%\;\)\(\&\@\#\$\^\*\{\}\+\-\~\|\?\？\\\[\]\=\.\\u]/,"");
        //this.$toast.center('文本包含特殊字符');
      });
      if(curVal != textValStorage){
        this.isComposite = false;
      }else{
        this.isComposite =true;
      }
    },
    currRate(curVal){
      let rateStorage = this.localGet("rateStorage") == null ? '' : this.localGet("rateStorage");
      if(curVal != rateStorage){
        this.isComposite = false;
      }else{
        this.isComposite =true;
      }
    },
    currVolume(curVal){
      let volumeStorage = this.localGet("volumeStorage") == null ? '' : this.localGet("volumeStorage");
      if(curVal != volumeStorage){
        this.isComposite = false;
      }else{
        this.isComposite =true;
      }
    },
    currCategoryItemId(curVal){
      let cat_idStorage = this.localGet("cat_idStorage") == null ? '' : this.localGet("cat_idStorage");
      if(curVal != cat_idStorage){
        this.isComposite = false;
      }else{
        this.isComposite =true;
      }
    }
  }
}
