<template>
    <div class="liui-page">
      <header class="liui-bar liui-pos-f">
        <div class="icon iconfont icon-fanhuitubiao" @click="gotoHome" style="display:none;"></div>
        <h1 class="title">智能语音实验室</h1>
      </header>
      <section class="liui-content">
        <div class="home-page">
          <div class="home-page-header">
            <p>欢迎使用</p>
            <div class="user-info nologin" v-if="!isLogin">
              <h2><router-link to="/login">点我登录</router-link></h2>
              <img src="../assets/images/avatar.png">
            </div>
            <div class="user-info" v-else @click="gotoUser">
              <h2><div>{{nickname}}</div></h2>
              <span :class="{ vip: isVip == 1 }">&nbsp;</span>
              <!-- <img :src="avatar" @click="loginOut"> -->
              <img :src="avatar">
            </div>
          </div>
          <div class="home-page-body">
            <ul>
              <li class="cell1">
                <router-link to="/tts">
                  <img class="icon" src="../assets/images/function01-1.png">
                  <img class="bg" src="../assets/images/function01.png">
                </router-link>
              </li>
              <li class="cell2">
                <router-link to="/compare">
                  <img class="title" src="../assets/images/index-title02.png">
                  <img class="bg" src="../assets/images/function02.png">
                </router-link>
              </li>
              <li class="cell3">
                <router-link to="/theme">
                  <div class="animate-header">
                    <img class="title" src="../assets/images/index-title03.png">
                  </div>
                  <!-- <div class="animate-body">
                    <div class="imgs">
                      <img class="bg1" src="../assets/images/function03-1.png">
                      <img class="bg2" src="../assets/images/function03-2.png">
                      <img class="bg3" src="../assets/images/function03-3.png">
                    </div>
                    <div class="diamond">
                      <img class="item item1" src="../assets/images/function03-small1.png">
                      <img class="item item2" src="../assets/images/function03-small2.png">
                      <img class="item item3" src="../assets/images/function03-small3.png">
                    </div>
                  </div> -->
                  <div class="animate-body">
                    <Swiper :autoPlay='true' :showIndicator='true' interval="3500" duration="500">
                      <Slide>
                        <img class="bg1" src="../assets/images/function03-1.png">
                      </Slide>
                      <Slide>
                        <img class="bg2" src="../assets/images/function03-2.png">
                      </Slide>
                      <Slide>
                        <img class="bg3" src="../assets/images/function03-3.png">
                      </Slide>
                    </Swiper>
                  </div>
                </router-link>
              </li>
            </ul>
          </div>
        </div>
      </section>
      <footer class="liui-footer" @click="gototop">
        <img src="../assets/images/logo_footer.png">
      </footer>
    </div>
</template>

<script>
import Swiper from '@/components/swiper.vue'
import Slide from '@/components/slide.vue'
export default {
  data () {
    return {
      isLogin: false,
      nickname: '',
      avatar: '',
      isVip: ''
    }
  },
  components: {
    Swiper,
    Slide
  },
  created(){
    let phone = this.$cookie.get("phone");
    if(phone){
      this.isLogin = true;
      this.nickname = this.$cookie.get("nickname");
      this.avatar = this.$cookie.get("avatar");
      this.isVip = parseInt(this.$cookie.get("isvip"));
    }else{
      this.isLogin = false;
    }
  },
  methods:{
    gotoHome(){
      if(window.location.origin == 'https://www.soundlab.heard-gl.com/'){
        window.location.href = 'https://www.soundlab.heard-gl.com/';
      }else{
        window.location.href = window.location.origin;
      }
    },
    gotoUser(){
      this.$router.push('/user');
    },
    gototop(){
      window.scrollTo(0,0);
      // if(document.documentElement.scrollTop>0){
      //   document.documentElement.scrollTop=0;
      // }
    }
  }
}
</script>

<style scoped>
.home-page-header{
  padding: 0 1rem 1rem 1rem;
}
.home-page-header > p{
  line-height: 3rem;
  font-size: 13px;
  color: #999;
}
.home-page-header .user-info{
  position: relative;
  padding: 0 3rem 1rem 0;
  line-height: 2rem;
}
.home-page-header .user-info h2{
  display: flex;
  max-width: 70%;
  font-size: 18px;
  line-height: 28px;
  align-items: center;
  overflow: hidden;white-space: nowrap;text-overflow: ellipsis;
}
.home-page-header .user-info h2 a{
  text-decoration: underline;
}
.home-page-header .user-info img{
  position: absolute;
  top: 0;
  right: 0;
  width: 53px;
  height: 53px;
  border-radius: 50%;
}
.home-page-header .user-info.nologin img{
  top: -22px;
}
.home-page-header .user-info span{
  display: block;
  width: 80px;
  height: 22px;
  margin-top: 0.2rem;
  background: url(../assets/images/vip.png) 0 0 no-repeat;
  background-size: 80px 44px;
}
.home-page-header .user-info span.vip{
  background: url(../assets/images/vip.png) 0 -22px no-repeat;
  background-size: 80px 44px;
}
.home-page-body{
  padding-bottom: 2rem;
  margin: 0 1rem;
}
.home-page-body li{
  position: relative;
  margin-bottom: 2rem;
  border-radius: 26px;
  box-shadow: 0 10px  35px rgba(0, 0, 0, 0.2);
  background-color: #fff;
  overflow: hidden;
}
.home-page-body li .title{
  width: 100%;
}
.home-page-body li .bg{
  width: 100%;
}
.home-page-body li.cell1 .icon{
  position: absolute;
  top: calc(50% - 80px);
  left: calc(50% - 80px);
  width: 160px;
  animation: rotate 6s 0s linear infinite;
}
.home-page-body li.cell2{
  padding: 0 0 0.2rem 0;
  background-color: #F5F5F5;
}
.home-page-body li.cell2 .title{
  background-color: #fff;
}
.home-page-body li.cell2 img.bg{
  width: 180%;
  transform: translateX(-2rem);
  animation: move 30s 0s linear infinite alternate;
}

@keyframes move {
  0% {
    transform: translateX(-2rem);
  }
  100% {
    transform: translateX(-40%);
  } 
}

.liui-footer img{
  width: 100%;
}

.home-page-body li.cell3 .animate-header .title{
  width: 100%;
}
.home-page-body li.cell3 .animate-body{
  position: relative;
  border-bottom-left-radius: 26px;
  border-bottom-right-radius: 26px;
  overflow: hidden;
}
.home-page-body li.cell3 .animate-body .imgs img{
  width: 100%;
  border-bottom-left-radius: 26px;
  border-bottom-right-radius: 26px;
}
.home-page-body li.cell3 .animate-body .imgs img.bg2,
.home-page-body li.cell3 .animate-body .imgs img.bg3{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 2;
  opacity: 0;
}
.home-page-body li.cell3 .animate-body .diamond{
  position: absolute;
  bottom: 1.5rem;
  left: 0;
  right: 0;
  z-index: 5;
  display: flex;
  padding: 0 .5rem;
}
.home-page-body li.cell3 .animate-body .diamond .item{
  flex: 1;
  width: calc(33.3333% - 1rem);
  height: calc((33.3333% - 1rem) * 1.3 / 1.1);
}

.animate-body .wh_slide{
  text-indent: -999rem;
}
.animate-body .wh_slide > img{
  width: 100%;
  border-bottom-left-radius: 26px;
  border-bottom-right-radius: 26px;
}
.animate-body .wh_indicator .wh_indicator_item{
  width: calc(33.3333% - 2rem);
  height: calc((33.3333% - 1rem) * 1.3 / 1.1);
  background: url(../assets/images/function03-small3.png) 0 0 no-repeat;
  
}


</style>



