<template>
    <div class="liui-page">
      <header class="liui-bar liui-pos-f">
        <div class="icon iconfont icon-fanhuitubiao" @click="gotoHome"></div>
        <h1 class="title">重设密码</h1>
      </header>
      <section class="liui-content">
        <div class="user-page liui-pos-f0">
          <div class="resetpass-cont">
            <div class="login-reg-body">
              <div class="login-reg-main">
                <div class="login-reg-item">
                  <input  @input="checkCaptcha" placeholder="请输入验证密码" type="text" v-model="captcha">
                  <span class="r-btn" @click="getCaptcha">{{countTxt}}</span>
                </div>
                <div class="login-reg-item item2">
                  <input v-show="!change" @input="checkPassword" placeholder="请输入6~12位数字或英文密码" type="password" v-model="password">
                  <input v-show="change" @input="checkPassword" placeholder="请输入6~12位数字或英文密码" type="text" v-model="password">
                  <i class="icon-clear" @click="clearPassword" v-show="password.length != 0"></i>
                  <i class="icon-change" @click="changePwShow" :class="{icon2: change }"></i>
                </div>
                <div class="login-reg-item login-reg-item-btn">
                  <button @click="resetComfirm" :class="{ active: password.length > 5 }">重设密码</button>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
    </div>
</template>

<script>
export default {
  data () {
    return {
      phone: '',
      captcha: '',
      password: '',
      countTxt: '获取验证码',
      countDown: 60,
      change: false
    }
  },
  created(){
    let phone = this.$cookie.get("phone");
    if(phone){
      this.phone = phone;
      this.getCaptcha();
    }else{
      this.$router.push('/login');
    }
  },
  methods:{
    gotoHome(){
      this.$router.push('/security');
    },
    checkCaptcha(){
      if(this.captcha.length > 4){
        this.captcha = this.captcha.substr(0,4);
      }
    },
    checkPassword(){

    },
    changePwShow(){
      this.change = !this.change;
    },
    clearPassword(){
      this.password = '';
    },
    getCaptcha(){
      if(!this.isCount){
        if(this.phone.length == 11){
          this.isCount = !this.isCount;
          this.$http('sms/send',{"mobile": this.phone,"event": 'resetpwd'}).then( res => {
            if(res.data.code == 1){
              this.$toast.center('发送验证码成功，注意查收~');
              this.countTxt = this.countDown + 's后重新发送';
              let clock = window.setInterval(() => {
                this.countDown--;
                this.countTxt = this.countDown + 's后重新发送'
                if (this.countDown < 0) {
                  window.clearInterval(clock);
                  this.countTxt = '发送验证码';
                  this.totalTime = 60;
                  this.isCount = false;
                }
              },1000);
            }else{
              this.$toast.center(res.data.msg);
              this.isCount = false;
            }
          }).catch( err => {
            console.log(err);
          });
        }else{
          this.$toast.center('请输入正确的11位手机号码');
        }
      }
    },
    resetComfirm(){
      if(this.password.length > 5 && this.password.length < 13){
        let opt = {
          "type": "mobile",
          "mobile": this.phone,
          "captcha": this.captcha,
          "newpassword": this.password,
          "captcha_type": "resetpwd"
        };
        this.$http('user/resetpwd',opt).then( res => {
          if(res.data.code == 1){
            this.$router.push('/login');
            this.$toast.center('修改密码成功');
            this.$cookie.delete("phone");
            this.$cookie.delete("avatar");
            this.$cookie.delete("nickname");
            this.$cookie.delete("isvip");
            this.$cookie.delete("token");
            this.$cookie.delete("defaultSpeed");
            this.$cookie.delete("defaultVolume");
          }else{
            this.$toast.center(res.data.msg);
          }
        }).catch( err => {
          console.log(err);
        });
      }else{
        this.$toast.center('请输入6~12位数字或英文密码');
      }
    }
  }
}
</script>

<style scoped>
.user-page{
  z-index: 5;
  background-color: #333;
}

.resetpass-cont{
  padding-top: 3rem;
}
.login-reg-item button{
  background-color: #999;
}
.login-reg-item button.active{
  background-color: #fff;
}
.login-reg-item .icon-clear{
    position: absolute;
    top: 0;
    right: 30px;
    width: 30px;
    height: 50px;
    background: url(../../assets/images/clear_icon.png) 9px 19px no-repeat;
    background-size: 12px 12px;
}
.login-reg-item .icon-change{
    position: absolute;
    top: 0;
    right: 0;
    width: 30px;
    height: 50px;
    background: url(../../assets/images/login-register-pw-hide.png) 5px 19px no-repeat;
    background-size: 20px 12px;
}
.login-reg-item .icon-change.icon2{
    background: url(../../assets/images/login-register-pw-show.png) 5px 19px no-repeat;
    background-size: 20px 12px;
}
.login-reg-item span.r-btn{
  position: absolute;
  top: 0;
  right: 0;
  line-height: 50px;
  color: #ddd;
  font-size: 1rem;
}
</style>



