import axios from 'axios'
import VueCookie from 'vue-cookie'
import router from '../router/index.js'

axios.interceptors.request.use(config=> {
  return config;
}, err=> {
  //Message.error({message: '请求超时!'});
  return Promise.resolve(err);
})
axios.interceptors.response.use(response => {
  if(response.data.code == 0){
    //网络服务不可用
  }
  return response;
}, err=> {
  if (err.response.status == 504||err.response.status == 404) {
    //Message.error({message: '服务异常,请稍后再试'});
  } else if (err.response.status == 403) {
    //Message.error({message: '权限不足,请联系管理员!'});
  }else if (err.response.status == 401) {
    VueCookie.delete("phone");
    VueCookie.delete("avatar");
    VueCookie.delete("nickname");
    VueCookie.delete("isvip");
    VueCookie.delete("token");
    VueCookie.delete("defaultSpeed");
    VueCookie.delete("defaultVolume");
    router.push('/login');
  }
  return Promise.resolve(err);
})

let base = process.env.API_PATH[window.location.origin];
//let base = process.env.API_PATH;

export const postRequest = (url, params) => {
  return axios({
    method: 'post',
    url: `${base}${url}`,
    data: params,
    headers: {
      'Content-Type': 'application/json;charset=utf-8',
      'token':VueCookie.get("token") ? VueCookie.get("token") : 'token'
    }
  });
}
export const fetchRequest = (url, formData) => {
  return axios({
    method: 'post',
    url: `${base}${url}`,
    data: formData,
    headers: {
      'Content-Type': 'multipart/form-data',
      'token':VueCookie.get("token") ? VueCookie.get("token") : 'token'
    }
  });
}


