<template>
    <div class="liui-page">
      <header class="liui-bar liui-pos-f">
        <div class="icon iconfont icon-fanhuitubiao" @click="gotoHome"></div>
        <h1 class="title">账号与安全</h1>
      </header>
      <section class="liui-content">
        <div class="setting-page liui-pos-f0">
          <div class="setting-list">
            <div class="cell" @click="linkTo('bind')">
              <label>手机绑定</label>
              <div class="txt txt-r">{{phone}}</div>
              <i class="iconfont icon-fanhuitubiao"></i>
            </div>
            <div class="cell" @click="linkTo('password')">
              <label>登录密码</label>
              <div class="txt txt-r">已设置</div>
              <i class="iconfont icon-fanhuitubiao"></i>
            </div>
          </div>
        </div>
      </section>
      <div v-if="show" class="show-layer liui-pos-f0">
        <div class="layer liui-pos-a"></div>
        <div class="main-body">
          <div class="title">换绑手机需要向您绑定的手机({{phone}})发送验证码</div>
          <div class="action">
            <span @click="() => show = false">取消</span>
            <span @click="comfirm">确定</span>
          </div>
        </div>
      </div>
    </div>
</template>

<script>
export default {
  data () {
    return {
      show: false,
      phone: '',
      routerStr: ''
    }
  },
  created(){
    let phone = this.$cookie.get("phone");
    if(!phone){
      this.$router.push('/login');
    }else{
      this.phone = phone;
    }
  },
  methods:{
    gotoHome(){
      this.$router.push('/user');
    },
    linkTo(str){
      if(str == 'bind'){
        this.routerStr = 'bind';
      }
      if(str == 'password'){
        this.routerStr = 'password';
        
      }
      this.show = true;
    },
    comfirm(){
      if(this.routerStr == 'bind'){
        this.$router.push('/bindmobile');
      }
      if(this.routerStr == 'password'){
        this.$router.push('/resetpass');
      }
    }
  }
}
</script>

<style scoped>
.setting-page{
  padding: 50px 0 0 0;
  background-color: #333;
}
.setting-list{
  padding: 0 1rem;
  border-top: 1px solid rgba(255,255,255,0.06);
}
.setting-page .cell{
  display: flex;
  padding: 1rem 0;
  line-height: 2rem;
  border-bottom: 1px solid rgba(255,255,255,0.06);
}
.setting-page .cell label{
  height: 1rem;
  font-size: 1rem;
  color: #fff;
}
.setting-page .cell .txt{
  flex: 1;
  margin: 0 0.5rem;
  color: #ccc;
}
.setting-page .cell i{
  transform: rotate(180deg);
  font-size: 1rem;
  color: #ccc;
}
.show-layer{
  z-index: 99;
}
.show-layer .layer{
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 2;
  background-color: rgba(0,0,0,0);
}
.show-layer .main-body{
  position: absolute;
  top: 50%;
  left: 50%;
  z-index: 5;
  width: 60%;
  border-radius: 5px;
  background-color: #fff;
  overflow: hidden;
  transform: translate3d(-50%,-50%,0)
}
.show-layer .main-body .title{
  padding: 1rem;
  text-align: center;
}
.show-layer .main-body .action{
  display: flex;
  margin-top: 1rem;
  border-top: 1px solid #eee;
}
.show-layer .main-body .action span{
  display: block;
  width: 50%;
  padding: 0.8rem;
  font-size: 1rem;
  color: #999;
  text-align: center;
}
.show-layer .main-body .action span + span{
  color: #306AFF;
  border-left: 1px solid #eee;
}
</style>



