<template>
  <div class="liui-page">
    <header class="liui-bar liui-pos-f">
      <div class="icon iconfont icon-fanhuitubiao" @click="gotoHome"></div>
      <h1 class="title">编辑资料</h1>
    </header>
    <section class="liui-content">
      <div class="user-page liui-pos-f0">
        <div class="camera-photo" ref="divGenres" v-show="isPhoto" @click="choiceImg">
          <div class="avatar-show">
            <img class="currAvatar" :src="oldAvatar">
            <div class="iconfont icon-xiangji"></div>
          </div>
          <div class="txt">点击修改头像</div>
          <input
            type="file"
            ref="uploadImage"
            @change="upload"
            accept="image/*"
            style="display: none;"
          >
          <!-- 开启摄像头 -->
          <!-- <input
            type="file"
            ref="uploadImage"
            @change="onFileChange"
            accept="image/*"
            capture="camera"
            style="display: none;"
          > -->
        </div>
        <div class="inp-body">
          <div class="lable">昵称</div>
          <div class="name"><input type="text" v-model="nickname" placeholder="请输入您的昵称"></div>
        </div>
      </div>
    </section>
    <footer class="user-page-footer">
      <button @click="save">保存</button>
    </footer>
  </div>
</template>

<script>
import qs from "qs";
export default {
  data() {
    return {
      nickname: '',
      avatar: '',
      oldAvatar: '',
      imgsrc: "", //上传的·图片的地址
      isPreview: false, //是否预览当前图片
      isPhoto: true,
      uploadFile: null
    };
  },
  created() {
    let phone = this.$cookie.get("phone");
    if (phone) {
      this.nickname = this.$cookie.get("nickname");
      this.oldAvatar = this.$cookie.get("avatar");
    } else {
      this.$router.push("/login");
    }
  },
  methods: {
    gotoHome() {
      this.$router.push("/user");
    },

    choiceImg() {
      let self = this;
      if (!window.plus) {
        self.addPic(); //如果不支持plus,就用本地相册上传即可
        return;
      }

      let title = "选择照片";
      let btns = ["拍照", "相册"];

      var func = function(e) {
        var index = e.index;

        if (index == 1) self.choiceCamera();
        if (index == 2) self.choicePic();
      };

      if (title && btns && btns.length > 0) {
        var btnArray = [];
        for (var i = 0; i < btns.length; i++) {
          btnArray.push({ title: btns[i] });
        }

        plus.nativeUI.actionSheet(
          {
            title: title,
            cancel: "取消",
            buttons: btnArray
          },
          function(e) {
            if (func) func(e);
          }
        );
      }
    },

    choiceCamera() {
      let self = this;
      var cmr = plus.camera.getCamera();
      cmr.captureImage(
        function(path) {
          plus.io.resolveLocalFileSystemURL(
            path,
            function(entry) {
              self.imgsrc = entry.toLocalURL();
            },
            function(e) {
              plus.nativeUI.toast("读取拍照文件错误：" + e.message);
            }
          );
        },
        function(e) {},
        { index: 1, filename: "_doc/camera/" }
      );
    },

    choicePic() {
      let self = this;
      plus.gallery.pick(
        function(p) {
          plus.io.resolveLocalFileSystemURL(
            p,
            function(entry) {
              self.imgsrc = entry.toLocalURL();
            },
            function(e) {
              plus.nativeUI.toast("读取拍照文件错误：" + e.message);
            }
          );
        },
        function(e) {
          plus.nativeUI.toast("读取拍照文件错误：" + e.message);
        },
        {
          filename: "_doc/camera/",
          filter: "image"
        }
      );
    },

    upload(e) {
      let self = this;
      let files = e.target.files || e.dataTransfer.files;
      if (!files.length) return;
      let file = files[0]; //File对象
      self.uploadFile = file;
      let reader = new FileReader(); //FileReader对象
      reader.readAsDataURL(file); //该方法会读取指定的 Blob 或 File 对象。读取操作完成的时候，readyState 会变成已完成（DONE），并触发 loadend 事件，同时 result 属性将包含一个data:URL格式的字符串（base64编码）以表示所读取文件的内容。

      
      self.$loading('上传中...');
      reader.onload = function(e) {
        self.imgsrc = e.target.result; //图片内容的base64编码
        let wt;
        if (window.plus) wt = plus.nativeUI.showWaiting();

        let img = new Image(),
          width = 120, //image resize   压缩后的宽
          height = 120, //image resize   压缩后的高
          quality = 0.2, //image quality  压缩质量
          canvas = document.createElement("canvas"),
          drawer = canvas.getContext("2d");
        img.src = self.imgsrc;
        img.setAttribute('crossorigin', 'anonymous');
        img.onload = function() {
          //利用canvas压缩图片
          canvas.width = width;
          //canvas.height = width * (img.height / img.width);
          canvas.height = height;
          drawer.drawImage(img, 0, 0, canvas.width, canvas.height);
          let base64 = canvas.toDataURL("image/*", quality);
          let pic = base64.split(",")[1]; //图片的base64编码内容
          let f = self.imgsrc;
          let filename = f.replace(f.substring(0, f.lastIndexOf("/") + 1), ""); //图片名称

          if (self.uploadFile !== null) {
            //addPic方法得到的图片文件
            filename = self.uploadFile.name;
            let reader = new FileReader();
            reader.readAsDataURL(self.uploadFile);
            reader.onload = function(e) {
              img.src = e.target.result;
              img.setAttribute('crossorigin', 'anonymous');
            };
            img.onload = function() {
              canvas.width = width;
              canvas.height = height;
              drawer.drawImage(img, 0, 0, canvas.width, canvas.height);
              base64 = canvas.toDataURL("image/*", quality);
              pic = base64.split(",")[1];
              self.imgsrc = pic;
            };
            self.imgsrc = base64;
            self.$http('common/qiNiuBase64Upload',{"name":self.imgsrc}).then( res => {
              let data = res.data;
              self.$loading.close();
              if(data.code == 1){
                self.isPreview = true;
                self.avatar = data.data.path;//不带域名地址的图片地址
                self.oldAvatar = self.imgsrc = data.data.domain_path;//带域名的图片地址
                self.$toast.center('上传图片成功');
              }else{
                self.$toast.center(data.msg);
              }
            }).catch( err => {
              console.log(err);
            });
          }else{
            self.$loading.close();
          }
        };
      }
    },

    addPic: function(e) {
      let els = this.$refs.divGenres.querySelectorAll("input[type=file]");
      els[0].click();
      return false;
    },
    save() {
      let self = this;
      let currImg = '';
      if(self.avatar != ''){//说明已经上传了图片
        currImg = self.avatar;
      }else{
        currImg = self.$cookie.get("avatar");
      }
      self.$http('user/setProfile',{"avatar":currImg,"nickname":self.nickname}).then( res => {
        let data = res.data;
        if(data.code == 1){
          self.$toast.center('修改成功');
          self.$cookie.set("avatar",self.imgsrc);
          self.$cookie.set("nickname",self.nickname);
          self.$router.push("/user");
        }else{
          self.$toast.center(data.msg);
        }
      }).catch( err => {
        console.log(err);
      });
    },
  }
};
</script>

<style scoped>
.user-page {
  z-index: 5;
  padding-top: 50px;
  background-color: #333;
}
.camera-photo{
  position:relative;
  padding: 2rem 1rem 1rem 1rem;
}
.camera-photo > .txt{
  line-height: 28px;
  color: #999;
  text-align: center;
  font-size: 12px;
}
.camera-photo .avatar-show{
  position: relative;
  width: 53px;
  height: 53px;
  margin: 0 auto;
  border-radius: 50%;
  overflow: hidden;
}
.camera-photo .avatar-show > img{
  position: relative;
  z-index: 2;
  width: 100%;
  height: 100%;
  border-radius: 50%;
}
.camera-photo .avatar-show .iconfont{
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 5;
  line-height: 50px;
  text-align: center;
  font-size: 20px;
  color: #eee;
  background-color: rgba(0,0,0,0.5);
}

.user-page .inp-body{
  display: flex;
  width: calc(100% - 2rem);
  margin: 0 auto;
  border-bottom: 1px solid rgba(255,255,255,0.2);
}
.user-page .inp-body .lable{
  width: 120px;
  line-height: 40px;
  color: #fff;
  font-size: 1rem;
}
.user-page .inp-body .name{
  flex: 1;
}
.user-page .inp-body .name input{
  width: 100%;
  height: 40px;
  border: 0;
  background: transparent;
  font-size: 1rem;
  color: #fff;
  text-align: right;
}
.user-page-footer {
  position: fixed;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 99;
  padding: 1rem;
  background-color: transparent;
}
.user-page-footer button {
  display: block;
  width: 100%;
  height: 3.5rem;
  border-radius: 5px;
  border: 0;
  background-color: #fff;
  color: #333;
  font-size: 16px;
}
</style>



