<template>
    <div class="liui-page">
      <header class="liui-bar liui-pos-f">
        <div class="icon iconfont icon-fanhuitubiao" @click="gotoHome"></div>
        <h1 class="title">设置</h1>
      </header>
      <section class="liui-content">
        <div class="setting-page liui-pos-f0">
          <router-link to="/security" class="link-to">
            <label class="iconfont icon-anquan"></label>
            <div class="txt">账号与安全</div>
            <i class="iconfont icon-fanhuitubiao"></i>
          </router-link>
        </div>
      </section>
    </div>
</template>

<script>
export default {
  data () {
    return {
    }
  },
  created(){
    let phone = this.$cookie.get("phone");
    if(phone){
      this.nickname = this.$cookie.get("nickname");
      this.avatar = this.$cookie.get("avatar");
    }else{
      this.$router.push('/login');
    }
  },
  methods:{
    gotoHome(){
      this.$router.push('/user');
    }
  }
}
</script>

<style scoped>
.setting-page{
  padding-top: 50px;
  background-color: #333;
  color: #fff;
}
.setting-page .link-to{
  display: flex;
  padding: 1rem;
  line-height: 1rem;
}
.setting-page .link-to label{
  width: 1rem;
  height: 1rem;
  font-size: 1.2rem;
  color: #999;
}
.setting-page .link-to .txt{
  flex: 1;
  margin: 0 0.5rem;
}
.setting-page .link-to i{
  transform: rotate(180deg);
  font-size: 1rem;
  color: #ccc;
}
</style>



