<template>
    <div class="liui-page">
      <header class="liui-bar liui-pos-f">
        <div class="icon iconfont icon-fanhuitubiao" @click="gotoHome"></div>
        <h1 class="title">手机换绑</h1>
      </header>
      <section class="liui-content">
        <div class="liui-pos-f0 bind-mobile-page">
          <div class="login-reg-body">
            <div class="login-reg-main">
              <div class="login-reg-item">
                <input placeholder="请输入手机号" type="number" v-model="phone">
              </div>
              <div class="login-reg-item">
                <input  @input="checkCaptcha" placeholder="请输入验证密码" type="text" v-model="captcha">
                <span class="r-btn" @click="getCaptcha">{{countTxt}}</span>
              </div>
              <div class="login-reg-item">
                <input @input="checkPhone" placeholder="请输入手机号" type="number" v-model="newPhone">
              </div>
              <div class="login-reg-item">
                <input  @input="checkCaptcha2" placeholder="请输入验证密码" type="text" v-model="captcha2">
                <span class="r-btn" @click="getCaptcha2">{{countTxt2}}</span>
              </div>
              <div class="login-reg-item login-reg-item-btn">
                <button @click="changeComfirm">确定</button>
              </div>
            </div>
          </div>
        </div>
      </section>
    </div>
</template>

<script>
export default {
  data () {
    return {
      phone: '',
      newPhone: '',
      captcha: '',
      captcha2: '',
      countTxt: '获取验证码',
      countTxt2: '获取验证码',
      countDown: 60,
      countDown2: 60,
      isCount: false,
      isCount2: false
    }
  },
  created(){
    let phone = this.$cookie.get("phone");
    if(!phone){
      this.$router.push('/login');
    }else{
      this.phone = phone;
      this.getCaptcha();
    }
  },
  methods:{
    gotoHome(){
      this.$router.push('/security');
    },
    checkCaptcha(){
      if(this.captcha.length > 4){
        this.captcha = this.captcha.substr(0,4);
      }
    },
    checkPhone(){
      if(this.phone.length > 11){
        this.phone = this.phone.substr(0,11);
      }
    },
    checkCaptcha2(){
      if(this.captcha2.length > 4){
        this.captcha2 = this.captcha2.substr(0,4);
      }
    },
    getCaptcha(){
      if(!this.isCount){
        if(this.phone.length == 11){
          this.isCount = !this.isCount;
          this.$http('sms/send',{"mobile": this.phone,"event": 'changemobile'}).then( res => {
            if(res.data.code == 1){
              this.$toast.center('发送验证码成功，注意查收~');
              this.countTxt = this.countDown + 's后重新发送';
              let clock = window.setInterval(() => {
                this.countDown--;
                this.countTxt = this.countDown + 's后重新发送'
                if (this.countDown < 0) {
                  window.clearInterval(clock);
                  this.countTxt = '发送验证码';
                  this.totalTime = 60;
                  this.isCount = false;
                }
              },1000);
            }else{
              this.$toast.center(res.data.msg);
            }
          }).catch( err => {
            console.log(err);
          });
        }else{
          this.$toast.center('请输入正确的11位手机号码');
        }
      }
    },
    nextComfirm(){
      if(this.captcha.length == 4){
        this.next = true;
      }else{
        this.$toast.center('请输入4位验证码');
      }
    },
    getCaptcha2(){
      if(!this.isCount2){
        if(this.newPhone.length == 11){
          this.isCount2 = !this.isCount2;
          this.$http('sms/send',{"mobile": this.newPhone,"event": 'bindmobile'}).then( res => {
            if(res.data.code == 1){
              this.$toast.center('发送验证码成功，注意查收~');
              this.countTxt2 = this.countDown2 + 's后重新发送';
              let clock = window.setInterval(() => {
                this.countDown2--;
                this.countTxt2 = this.countDown2 + 's后重新发送'
                if (this.countDown2 < 0) {
                  window.clearInterval(clock);
                  this.countTxt2 = '发送验证码';
                  this.totalTime2 = 60;
                  this.isCount2 = false;
                }
              },1000);
            }else{
              this.$toast.center(res.data.msg);
            }
          }).catch( err => {
            console.log(err);
          });
        }else{
          this.$toast.center('请输入正确的11位手机号码');
        }
      }
    },
    changeComfirm(){
      let _this = this;
      if(_this.captcha != '' && _this.captcha.length == 4){
        if(_this.newPhone != '' && _this.newPhone.length == 11){
          if(_this.captcha2 != '' && _this.captcha2.length == 4){
            let opt = {
              "mobile": this.phone,
              "captcha": this.captcha,
              "new_mobile": this.newPhone,
              "new_captcha": this.captcha2
            };
            this.$http('user/replaceBindMobile',opt).then( res => {
              if(res.data.code == 1){
                _this.$cookie.delete("phone");
                _this.$cookie.delete("avatar");
                _this.$cookie.delete("nickname");
                _this.$cookie.delete("isvip");
                _this.$cookie.delete("token");
                _this.$cookie.delete("defaultSpeed");
                _this.$cookie.delete("defaultVolume");
                _this.$toast.center('换绑成功');
                _this.$router.push('/login');
              }else{
                this.$toast.center(res.data.msg);
              }
            }).catch( err => {
              console.log(err);
            });
          }else{
            _this.$toast.center('请输入正确的4位验证码');
          }
        }else{
          _this.$toast.center('请输入正确的11位手机号码');
        }
      }else{
        _this.$toast.center('请输入正确的4位验证码');
      }
    }
  }
}
</script>

<style scoped>
.bind-mobile-page{
  padding-top: 50px;
  background-color: #333;
}
.login-reg-item span.r-btn{
  position: absolute;
  top: 0;
  right: 0;
  line-height: 50px;
  color: #ddd;
  font-size: 1rem;
}
.login-reg-item button.noclick{
  background-color: #999;
}
</style>



