<template>
    <div class="liui-page">
      <header class="liui-bar liui-pos-f">
        <div class="icon iconfont icon-fanhuitubiao" @click="gotoHome"></div>
        <h1 class="title">TTS对比</h1>
      </header>
      <section class="liui-content">
        <div class="tts-page compare-page">
          <div class="tts-page-header">
            <!-- <p>TTS对比</p> -->
            <h2>我的“标准”一点也不低</h2>
          </div>
          <div class="compare-page-body">
            <div class="compare-item liui-pos-r">
              <div class="compare-item-mian">
                <div class="container">
                  <div class="title">{{ttsText}}</div>
                  <div class="protype">
                    <img class="bg" src="../../assets/images/tts-compare-audio-bg.png">
                    <div class="logo" :class="{ rotate: isPlaying }">
                      <img src="../../assets/images/tts-compare-audio-cilun.png">
                      <img :src="currCompareImg">
                    </div>
                    <div class="logo" :class="{ rotate: isPlaying }">
                      <img src="../../assets/images/tts-compare-audio-cilun.png">
                      <img :src="currCompareImg">
                    </div>
                    <button @click="playAudio" class="btn" :class="{ active: isPlaying }"></button>
                    <div class="pre-compare" @click="preCompare"><i class="iconfont icon-fanhuitubiao"></i></div>
                    <div class="next-compare" @click="nextCompare"><i class="iconfont icon-fanhuitubiao"></i></div>
                  </div>
                  <div class="like" :class="{ active: isLiked }" @click="likeComfirm">
                    <div class="icon"></div>
                    <span>{{like}}</span>
                  </div>
                </div>
                <div class="dots dot1"></div>
                <div class="dots dot2"></div>
                <div class="dots dot3"></div>
                <div class="dots dot4"></div>
                <img class="bg2" src="../../assets/images/tts-compare-audio-bg2.png">
              </div>
              <div class="rangle"></div>
            </div>
          </div>
          <div class="compare-list">
            <ul>
              <li><img src="../../assets/images/blank.png"></li>

              <li v-for="(item,index) in compareList" :class="{active: item.id == currCompareIndex }" @click="chooseCompareItem(item)"><img :src="item.logo_domain"></li>

            </ul>
          </div>
          <div class="ranking" :class="{ opening: isRanking }">
            <div class="title" @click="toggleRanking">
              <div class="icon"></div>
              <span>当前排名</span>
            </div>
            <div class="ranking-list">
              <ul>
                <li v-for="(item,index) in rankList">
                  <div class="ranking-img"><img :src="item.logo_domain"></div>
                  <div class="ranking-line"><div class="line" :style="'width:'+item.line+'%;'"></div></div>
                  <div class="ranking-num">{{item.total_likes}}票</div>
                </li>
              </ul>
            </div>
          </div>
          <div class="ranking-layer liui-pos-f0" v-if="isRanking" @click="toggleRanking"></div>
        </div>
      </section>
      <audio ref="player" :src="playSrc" @ended="ended()"></audio>
      <footer class="fixed-bottom-nav">
        <ul>
          <li class="item">
            <router-link to="/tts">
              <div class="iconfont icon-biaoqianlan-yuyinhecheng1"></div>
              <span>语音合成</span>
            </router-link>
          </li>
          <li class="item active">
            <router-link to="/compare">
              <div class="iconfont icon-biaoqianlan-TTSduibi1"></div>
              <span>TTS对比</span>
            </router-link>
          </li>
          <li class="item">
            <router-link to="/theme">
              <div class="iconfont icon-biaoqianlan-zhutizhanshi1"></div>
              <span>主题展示</span>
            </router-link>
          </li>
        </ul>
      </footer>
    </div>
</template>

<script>
export default {
  data () {
    return {
      ttsText: '',
      isPlaying: false,
      playSrc: '',
      like: 0,
      isLiked: false,
      isRanking: false,
      currCompareIndex: '',
      currCompareItem: '',
      currCompareImg: '',
      compareList: [],
      compareAllList: [],
      currCompareListIndex: 0,
      compareAllListLength: '',
      rankingNum: 5,
      rankList:[]
    }
  },
  created(){
    let phone = this.$cookie.get("phone");
    if(!phone){
      this.$router.push('/login');
    }else{
      let _this = this;
      _this.$loading('数据加载中...');
      _this.$http('Ttscontrast/getList',{"page": "1","pagesize": "99"}).then( res => {
        _this.$loading.close();
        let data = res.data;
        if(data.code == 1){
          let compareData = data.data.data[0];
          let player = _this.$refs.player;
          _this.compareAllList = data.data.data;
          _this.compareAllListLength = data.data.data.length;
          _this.ttsText = compareData.content;
          _this.compareList = compareData.resources_list;
          _this.currCompareIndex = _this.compareList[0].id;
          _this.currCompareImg = _this.compareList[0].logo_domain;
          _this.like = _this.compareList[0].total_likes;
          _this.playSrc = _this.compareList[0].path_domain;

          let baseNum = _this.compareList[0].total_likes;
          for(let i=0;i<_this.rankingNum;i++){
            _this.rankList[i] = _this.compareList[i];
            _this.rankList[i].line = _this.rankList[i].total_likes / baseNum * 100;
          }
        }
      }).catch( err => {
        console.log(err);
      });
    }
  },
  methods:{
    gotoHome(){
      this.$router.push('/');
    },
    playAudio(){
      let player = this.$refs.player;
      this.isPlaying = !this.isPlaying;
      if(this.isPlaying){
        player.play();
      }else{
        player.pause();
      }
    },
    likeComfirm(){
      let _this = this;
      if(_this.isLiked){//取消赞
        _this.$http('Userlikescontrast/whetherLike',{"contrast_id": _this.currCompareIndex+"","is_like":"0"}).then( res => {
          let data = res.data;
          if(data.code == 1){
            _this.like -= 1;
            _this.isLiked = false;
            _this.$toast.center('已取消点赞');
            for(let i=0;i<_this.compareList.length;i++){
              if(_this.currCompareIndex == _this.compareList[i].id){
                _this.compareList[i].total_likes = _this.like;
                _this.rankList[i].total_likes = _this.like;
              }
            }
          }
        }).catch( err => {
          console.log(err);
        });
      }else{
        _this.$http('Userlikescontrast/whetherLike',{"contrast_id": _this.currCompareIndex+"","is_like":"1"}).then( res => {
          let data = res.data;
          if(data.code == 1){
            _this.like += 1;
            _this.isLiked = true;
            _this.$toast.center('已点赞');
            for(let i=0;i<_this.compareList.length;i++){
              if(_this.currCompareIndex == _this.compareList[i].id){
                _this.compareList[i].total_likes = _this.like;
                _this.rankList[i].total_likes = _this.like;
              }
            }
          }
        }).catch( err => {
          console.log(err);
        });
      }
      
    },
    toggleRanking(){
      this.isRanking = !this.isRanking;
    },
    chooseCompareItem(item){
      let player = this.$refs.player;
      this.isPlaying = false;
      this.isLiked = false;
      this.currCompareIndex = item.id;
      this.like = item.total_likes;
      this.currCompareImg = item.logo_domain;
      this.currCompareItem = item;
      this.playSrc = item.path_domain;
      player.pause();
    },
    //前一组
    preCompare(){
      this.isPlaying = false;
      if(this.currCompareListIndex == 0){
        this.currCompareListIndex = this.compareAllListLength - 1;
      }else{
        this.currCompareListIndex -= 1;
      }
      this.compareList = this.compareAllList[this.currCompareListIndex].resources_list;
      this.ttsText = this.compareAllList[this.currCompareListIndex].content;
      this.currCompareIndex = this.compareList[0].id;
      this.currCompareImg = this.compareList[0].logo_domain;
      this.like = this.compareList[0].total_likes;
      this.playSrc = this.compareList[0].path_domain;

      let baseNum = this.compareList[0].total_likes;
      for(let i=0;i<this.rankingNum;i++){
        this.rankList[i] = this.compareList[i];
        this.rankList[i].line = this.rankList[i].total_likes / baseNum * 100;
      }
      
    },
    //后一组
    nextCompare(){
      this.isPlaying = false;
      if(this.currCompareListIndex == (this.compareAllListLength -1)){
        this.currCompareListIndex = 0;
      }else{
        this.currCompareListIndex += 1;
      }
      this.compareList = this.compareAllList[this.currCompareListIndex].resources_list;
      this.ttsText = this.compareAllList[this.currCompareListIndex].content;
      this.currCompareIndex = this.compareList[0].id;
      this.currCompareImg = this.compareList[0].logo_domain;
      this.like = this.compareList[0].total_likes;
      this.playSrc = this.compareList[0].path_domain;

      let baseNum = this.compareList[0].total_likes;
      for(let i=0;i<this.rankingNum;i++){
        this.rankList[i] = this.compareList[i];
        this.rankList[i].line = this.rankList[i].total_likes / baseNum * 100;
      }
    },
    ended(){
      this.isPlaying = false;
      console.log("---------------------------播放完毕了");
    }
  }
}
</script>

<style scoped>
.liui-content{
  background-color: #fff;
}
.compare-page{
  padding-bottom: 8.5rem;
  background-color: #fff;
}
.compare-page .tts-page-header{
  padding: 1rem 1rem 0 1rem;
  background-color: #fff;
}
.compare-page-body{
  padding: 1rem 1rem 2rem 1rem;
  background-color: #fff;
}
.compare-page-body .compare-item-mian{
  position: relative;
  z-index: 3;
  padding: 1.2rem 1.2rem 2rem 1.2rem;
  border-radius: 10px;
  background-color: #f9f9f9;
  box-shadow: 0 8px 36px rgba(0,0,0,0.26);
}
.compare-page-body .compare-item-mian .container{
  padding: 1rem 1rem 0 1rem;
  border-radius: 10px;
  background-color: #fff;
  box-shadow: 0 0 50px rgba(0,0,0,0.06);
}
.compare-page-body .compare-item-mian .dots{
  position: absolute;
  width: 15px;
  height: 15px;
  background: url(../../assets/images/audio-luosi.png) 0 0 no-repeat;
  background-size: 15px 15px;
}
.compare-page-body .compare-item-mian .dots.dot1{
  top: 5px;
  left: 5px;
}
.compare-page-body .compare-item-mian .dots.dot2{
  top: 5px;
  right: 5px;
}
.compare-page-body .compare-item-mian .dots.dot3{
  bottom: 5px;
  left: 5px;
}
.compare-page-body .compare-item-mian .dots.dot4{
  bottom: 5px;
  right: 5px;
}
.compare-page-body .compare-item-mian .bg2{
  position: absolute;
  left: 25%;
  bottom: 0;
  width: 50%;
}
.compare-page-body .compare-item .rangle{
  position: relative;
  z-index: 2;
  width: 90%;
  height: 100px;
  margin: -90px auto 0 auto;
  border-radius: 10px;
  background-color: #fff;
  content: " ";
  box-shadow: 0 8px 36px rgba(0,0,0,0.26);
}
.compare-page-body .compare-item .title{
  padding-bottom: 1rem;
  font-size: 12px;
  color: #555;
}
.compare-page-body .compare-item .protype{
  position: relative;
  width: 262px;
  height: 60px;
  margin: 0 auto;
}
.compare-page-body .compare-item .protype .bg{
  position: relative;
  z-index: 2;
  height: 60px;
}
.compare-page-body .compare-item .protype .logo{
  position: absolute;
  top: 3px;
  left: 3px;
  z-index: 5;
  width: 54px;
  height: 54px;
  border-radius: 50%;
  background-color: #fff;
}
.compare-page-body .compare-item .protype .logo + .logo{
  left: auto;
  right: 3px;
}
.compare-page-body .compare-item .protype .logo > img{
  position: absolute;
  top: 0;
  left: 0;
  z-index: 5;
  width: 100%;
  height: 100%;
  border-radius: 50%;
}
.compare-page-body .compare-item .protype .logo > img:nth-child(2){
  top: 5px;
  left: 5px;
  z-index: 6;
  width: 44px;
  height: 44px;
  opacity: 0.85;
  background-color: #fff;
}
.compare-page-body .compare-item .protype .logo.rotate{
  animation: spin 3.2s linear infinite;
}
@keyframes spin {
  0%{
    transform: rotate(0deg);
  }
  100%{
    transform: rotate(360deg);
  }
}
.compare-page-body .compare-item .protype .pre-compare,
.compare-page-body .compare-item .protype .next-compare{
  position: absolute;
  top: 15px;
  left: -3rem;
  z-index: 6;
  width: 31px;
  height: 31px;
  border-radius: 50%;
  border:1px solid #dcdcdc;
  color: #999;
  line-height: 31px;
  text-align: center;
  text-indent: 4px;
}
.compare-page-body .compare-item .protype .next-compare{
  left: auto;
  right: -3rem;
  transform: rotate(180deg);
}
.compare-page-body .compare-item .protype i{
  color: #dcdcdc;
  font-weight: bold;
}

.compare-page-body .compare-item .protype .btn{
  position: absolute;
  top: 11px;
  left: calc(50% - 19.5px);
  z-index: 6;
  width: 39px;
  height: 39px;
  background: url(../../assets/images/tts-compare-play-stop-icon.png) 0 0 no-repeat;
  background-size: 39px 79px;
}
.compare-page-body .compare-item .protype .btn.active{
  background: url(../../assets/images/tts-compare-play-stop-icon.png) 0 -39px no-repeat;
  background-size: 39px 79px;
}
.compare-page-body .compare-item .like{
  padding: 0.8rem 0 0 0;
  text-align: center;
}
.compare-page-body .compare-item .like .icon{
  width: 26px;
  height: 26px;
  margin: 0 auto;
  background: url(../../assets/images/tts-compare-like.png) 0 0 no-repeat;
  background-size: 26px 52px;
}
.compare-page-body .compare-item .like span{
  line-height: 1.5rem;
  font-size: 12px;
  color: #999;
  transition: all .25s cubic-bezier(.4,0,.2,1);
}
.compare-page-body .compare-item .like.active .icon{
  background: url(../../assets/images/tts-compare-like.png) 0 -26px no-repeat;
  background-size: 26px 52px;
}
.compare-page-body .compare-item .like.active span{
  color: #3C7BFF;
}

.compare-list{
  width: 100%;
  overflow-x: auto;
}
.compare-list ul{
  width: 120%;
  font-size: 0;
  line-height: 0;
}
.compare-list ul li{
  position: relative;
  display: inline-block;
  width: 13.5%;
  margin: 0 0 5px 5px;
  border:1px solid #eee;
  border-radius: 15px;
  overflow: hidden;
}
.compare-list ul li.active:before{
  /* border-color: #3C7BFF; */
  position: absolute;
  top: 0;
  left: 0;
  z-index: 3;
  width: 100%;
  height: 100%;
  border-radius: 15px;
  background-color: rgba(232,232,232,0.5);
  content: " ";
  /* transform: scale(1.2) */
}
.compare-list ul li img{
  width: 100%;
}
.compare-list ul li:first-child{
  margin-left: calc(-9% - 5px);
}
.compare-list ul li:nth-child(6+n){
  margin-bottom: 0;
}
.ranking{
  position: absolute;
  bottom: 3rem;
  left: 0;
  right: 0;
  z-index: 9;
  padding-bottom: .5rem;
  margin: 1.5rem 1rem 0 1rem;
  border-radius: 10px;
  background: #fff;
  box-shadow: 0 0 50px rgba(0,0,0,0.1);
  transition: all .25s cubic-bezier(.4,0,.2,1);
  transform: translateY(0);
}
.ranking-layer{
  top: 0;
  left: 0;
  right: 0;
  bottom: 50px;
  z-index: 8;
}
.ranking .title{
  text-align: center;
}
.ranking .title .icon{
  width: 36px;
  height: 36px;
  margin: 0 auto;
  /* background: url(../../assets/images/tts-compare-ranking.png) 0 0 no-repeat;
  background-size: 36px 72px; */
  background: url(../../assets/images/tts-compare-ranking.png) 0 -36px no-repeat;
  background-size: 36px 72px;
  transform: translateY(-16px);
  /* transition: all .25s cubic-bezier(.4,0,.2,1); */
}
.ranking .title span{
  display: block;
  margin-top: -16px;
  font-size: 13px;
  color: #999;
}
.ranking.opening .title .icon{
  background: url(../../assets/images/tts-compare-ranking.png) 0 -36px no-repeat;
  background-size: 36px 72px;
}
.ranking .ranking-list{
  height: 1px;
  padding-top: 1px;
  overflow: hidden;
  transition: all .25s cubic-bezier(.4,0,.2,1);
}
.ranking.opening{
  padding: 0;
  transform: translateY(-1rem);
}
.ranking.opening .ranking-list{
  height: auto;
}
.ranking .ranking-list ul{
  margin-top: 1px;
  padding: 0 2rem 1rem 2rem;
}
.ranking-list li{
  display: flex;
  align-items:center;
}
.ranking-list li + li{
  margin-top: 1rem;
}
.ranking-img img{
  width: 2.8rem;
  padding: 0.2rem;
  border:1px solid #eee;
  border-radius: .5rem;
}
.ranking-line{
  width: 50%;
  margin: 0 1rem;
}
.ranking-line .line{
  display: block;
  height: 4px;
  background-color: #CCDCFE;
  content: " ";
}
.ranking-num{
  font-size: 12px;
}
</style>



