<template>
    <div class="liui-page">
      <header class="liui-bar liui-pos-f">
        <div class="icon iconfont icon-fanhuitubiao" @click="gotoHome"></div>
        <h1 class="title">主题展示</h1>
      </header>
      <section class="liui-content">
        <div class="tts-page theme-page liui-pos-f" :style="'background-image:url('+fullBg+');'">
          <div class="tts-page-header">
            <!-- <p>主题展示</p> -->
            <h2>让你体验“身临其境”</h2>
          </div>
          <div class="theme-page-body">
            <div class="theme-name"><span>{{themeName}}</span><label></label></div>
            <div class="theme-list">
              <ul ref="themeList">
                <li ref="themeItem" v-for="(item,index) in themeList" @click="chooseThemeItem(item)" :class="{ active: item.id == currThemeId }">
                  <div class="info">
                    <h3 class="txt-ellipsis">{{item.name}}</h3>
                    <p>已有{{item.plays}}人收听</p>
                  </div>
                  <div class="img">
                    <img :src="item.thumb_img_domain">
                  </div>
                </li>
              </ul>
            </div>
            <div class="theme-player" :class="{ playing:isPlaying }">
              <aplayer
                ref="player"
                theme="pic"
                @play="onPlay"
                @pause="onPause"
                :music="aplayerList[0]"/>
              
              <div class="theme-player-icon"></div>
            </div>
          </div>
          <div class="gradient-bg"></div>
          <div class="gradient-bg2"></div>
        </div>
      </section>
      <footer class="fixed-bottom-nav">
        <ul>
          <li class="item">
            <router-link to="/tts">
              <div class="iconfont icon-biaoqianlan-yuyinhecheng1"></div>
              <span>语音合成</span>
            </router-link>
          </li>
          <li class="item">
            <router-link to="/compare">
              <div class="iconfont icon-biaoqianlan-TTSduibi1"></div>
              <span>TTS对比</span>
            </router-link>
          </li>
          <li class="item active">
            <router-link to="/theme">
              <div class="iconfont icon-biaoqianlan-zhutizhanshi1"></div>
              <span>主题展示</span>
            </router-link>
          </li>
        </ul>
      </footer>
    </div>
</template>

<script>
import Aplayer from 'vue-aplayer'
Aplayer.disableVersionBadge = true
export default {
  components: {
    Aplayer
  },
  data () {
    return {
      fullBg: '',
      themeName: '默认标题',
      currThemeId: '',
      isPlaying: false,
      themeList:[],
      aplayerList:[
        {
          title: '',
          artist: ' ',
          src: 'https://www.sounddat.heard-gl.com/demo.mp3',
          pic: ''
        }
      ]
    }
  },
  created () {
    let phone = this.$cookie.get("phone");
    if(!phone){
      this.$router.push('/login');
    }else{
      let _this = this;
      _this.$loading('数据加载中...');
      _this.$http('Ttstheme/getList',{"page": "1","pagesize": "99"}).then( res => {
        _this.$loading.close();
        let data = res.data;
        if(data.code == 1){
          let themeData = data.data.data;
          let aplayer = this.$refs.player;
          _this.themeList = themeData;
          _this.fullBg = themeData[0].img_domain;
          _this.currThemeId = themeData[0].id;
          _this.themeName = themeData[0].name;
          _this.$nextTick(()=>{
            _this.InitTabScroll();
            document.addEventListener('WeixinJSBridgeReady',function(){aplayer.load()},false);
            aplayer.audio.src = themeData[0].audio_path_domain;
            aplayer.music.title = themeData[0].name;
            aplayer.audio.play();
          });
          _this.$http('Ttstheme/getContent',{"theme_id": themeData[0].id}).then( res => {
            let data = res.data;
            if(data.code == 1){
              _this.themeList[0].plays +=1;
            }
          }).catch( err => {
            console.log(err);
          });
        }
      }).catch( err => {
        console.log(err);
      });
    }
  },
  methods:{
    gotoHome(){
      this.$router.push('/');
    },
    InitTabScroll(){
      let width= 0;
      for (let  i = 0; i <this.themeList.length; i++) {
          width+=this.$refs.themeItem[0].getBoundingClientRect().width;
      }
      width +=70;
      this.$refs.themeList.style.width=width+'px';
      // this.$nextTick(()=>{
      //   if (!this.scroll) {
      //     this.scroll=new BScroll(this.$refs.themeList, {
      //       startX:0,
      //       click:true,
      //       scrollX:true,
      //       scrollY:false,
      //       eventPassthrough:'vertical'
      //     });
      //   }else{
      //     this.scroll.refresh();
      //   }
      // });
    },
    onPlay(){
      this.isPlaying = true;
    },
    onPause(){
      this.isPlaying = false;
    },
    chooseThemeItem(item){
      let _this = this;
      _this.fullBg = item.img_domain;
      _this.themeName = item.name;
      _this.currThemeId = item.id;
      //_this.isPlaying = true;
      let aplayer = _this.$refs.player;
      aplayer.audio.src = item.audio_path_domain;
      aplayer.music.title = item.name;
      aplayer.audio.play();
      _this.$http('Ttstheme/getContent',{"theme_id": item.id}).then( res => {
        let data = res.data;
        if(data.code == 1){
          for(let i=0;i<_this.themeList.length;i++){
            if(item.id == _this.themeList[i].id){
              _this.themeList[i].plays +=1;
            }
          }
        }
      }).catch( err => {
        console.log(err);
      });
    }
  }
}
</script>

<style scoped>

.theme-page{
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 2;
  padding-top: 50px;
  background-repeat: no-repeat;
  background-position: 0 0;
  background-size: 100% 100%;
  background-color: #333;
}
.theme-page .gradient-bg{
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1;
  height: 264px;
  background: linear-gradient(rgba(0,0,0,0.42),rgba(0,0,0,0));
}
.theme-page .gradient-bg2{
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 1;
  height: 264px;
  background: linear-gradient(rgba(0,0,0,0),rgba(0,0,0,0.42));
}
.theme-page .tts-page-header{
  position: relative;
  z-index: 5;
  padding: 1rem 1rem 0 1rem;
}
.theme-page .tts-page-header p{
  color: #fff;
  border-color: #fff;
}
.theme-page .tts-page-header h2{
  color: #fff;
}
.theme-list{
  position: absolute;
  bottom: 10.5rem;
  left: 0;
  right: 0;
  z-index: 5;
  padding-top: 1rem;
  overflow-x: scroll;
}
.theme-list ul{
  min-width: 100%;
  font-size: 0;
}
.theme-list li{
  position: relative;
  z-index: 6;
  display: inline-block;
  width: 120px;
  padding: .8rem;
  border-radius: 5px;
  margin: 0 0 0 .8rem;
  background-color: rgba(0,0,0,0.65);
  color: #fff;
  box-shadow: 0 7px 6px rgba(0,0,0,0.06);
  transition: all .15s cubic-bezier(.4,0,.2,1);
}
.theme-list li .info h3{
  font-size: 1rem;
  line-height: 2rem;
}
.theme-list li .info p{
  font-size: 10px;
  color: #fff;
}
.theme-list li .img img{
  padding-top: .5rem;
  width: 100%;
  height: 80px;
}
.theme-list li.active{
  transform: translateY(-1rem);
}

.theme-page-body .theme-name{
  padding: 2rem 1rem;
  color: #fff;
}
.theme-page-body .theme-name span{
  display: inline-block;
  font-size: 14px;
  line-height: 20px;
  text-shadow: 0 3px 3px rgba(0,0,0,0.18);
}
.theme-page-body .theme-name label{
  display: inline-block;
  width: 30px;
  height: 20px;
  margin-left: .3rem;
  background: url(../../assets/images/tts-theme-name-bg.png) 0 11px no-repeat;
  background-size: 30px 9px;
  box-shadow: 0 3px 3px rgba(0,0,0,0.18);
}
.theme-player{
  position: absolute;
  bottom: 51px;
  left: 0;;
  right: 0;
  z-index: 5;
}
.theme-player-icon{
    position: absolute;
    top: 10px;
    left: .8rem;
    z-index: 9;
    width: 66px;
    height: 66px;
    background: url(../../assets/images/tts-theme-player-bg.png) 0 0 no-repeat;
    background-size: 66px 66px;
}
.theme-player.playing .theme-player-icon{
  -webkit-animation: rotate 5s 0s linear infinite;
          animation: rotate 5s 0s linear infinite;
}
.theme-player .aplayer{
  margin: 0;
  border-radius: 0;
  background-color: transparent;
}
.fixed-bottom-nav{
  background-color: transparent;
}
.fixed-bottom-nav:after{
    border-top:1px solid rgba(255,255,255,0.25);
}
.fixed-bottom-nav .item .iconfont,.fixed-bottom-nav .item span{
  color: rgba(255,255,255,0.65)
}
.fixed-bottom-nav .item.active .iconfont,.fixed-bottom-nav .item.active span{
  color: #fff
}
</style>



