import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/views/Index'
import Login from '@/views/Login'
import Register from '@/views/Register'
import TTS from '@/views/tts/Index'
import Compare from '@/views/compare/Index'
import Theme from '@/views/theme/Index'
import User from '@/views/user/Index'
import Edit from '@/views/user/Edit'
import ResetPass from '@/views/user/ResetPass'
import Recode from '@/views/user/Recode'
import Setting from '@/views/user/Setting'
import Security from '@/views/user/Security'
import BindMobile from '@/views/user/BindMobile'


Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Index',
      component: Index
    },
    {
      path: '/login',
      name: 'Login',
      component: Login
    },
    {
      path: '/register',
      name: 'Register',
      component: Register
    },
    {
      path: '/tts',
      name: 'TTS',
      component: TTS
    },
    {
      path: '/compare',
      name: 'Compare',
      component: Compare
    },
    {
      path: '/theme',
      name: 'Theme',
      component: Theme
    },
    {
      path: '/user',
      name: 'User',
      component: User
    },
    {
      path: '/edit',
      name: 'Edit',
      component: Edit
    },
    {
      path: '/resetpass',
      name: 'ResetPass',
      component: ResetPass
    },
    {
      path: '/record',
      name: 'Recode',
      component: Recode
    },
    {
      path: '/setting',
      name: 'Setting',
      component: Setting
    },
    {
      path: '/security',
      name: 'Security',
      component: Security
    },
    {
      path: '/bindmobile',
      name: 'BindMobile',
      component: BindMobile
    }
  ]
})



