<template>
    <div class="liui-page">
      <header class="liui-bar liui-pos-f">
        <div class="icon iconfont icon-fanhuitubiao" @click="gotoHome"></div>
        <h1 class="title">智能语音实验室</h1>
        <div class="liu-bar-r-txt"><router-link to="/register">注册</router-link></div>
      </header>
      <section class="liui-content">
        <div class="login-reg-page liui-pos-f">
          <img class="bg" src="../assets/images/login-register-bg.png">
          <div class="login-reg-body">
            <div class="login-reg-header">
              <!-- <p>注册 & 登录</p> -->
              <h2>账号密码登录</h2>
            </div>
            <div class="login-reg-main">
              <div class="login-reg-item">
                <input @input="checkPhone" placeholder="请输入手机号" type="number" v-model="phone">
              </div>
              <div class="login-reg-item item2">
                <input v-show="!change" @input="checkPassword" placeholder="请输入密码" type="password" v-model="password">
                <input v-show="change" @input="checkPassword" placeholder="请输入密码" type="text" v-model="password">
                <i class="icon-clear" @click="clearPassword" v-show="password.length != 0"></i>
                <i class="icon-change" @click="changePwShow" :class="{icon2: change }"></i>
              </div>
              <div class="login-reg-item login-reg-item-btn">
                <button @click="LoginComfirm">登录</button>
              </div>
              <div class="login-reg-item liui-row" v-show="0">
                <div class="liui-col-xs-6"><router-link to="">验证码登录</router-link></div>
                <div class="liui-col-xs-6 txt-r"><router-link to="">忘记密码?</router-link></div>
              </div>
            </div>
          </div>
        </div>
      </section>
    </div>
</template>

<script>
export default {
  data () {
    return {
      phone: '',
      password: '',
      change: false
    }
  },
  created(){
    this.$loading.close();
    let phone = this.$cookie.get("phone");
    if(phone){
      this.$router.push('/');
    }
  },
  methods:{
    gotoHome(){
      this.$router.push('/');
    },
    checkPhone(){
      if(this.phone.length > 11){
        this.phone = this.phone.substr(0,11);
      }
    },
    checkPassword(){

    },
    changePwShow(){
      this.change = !this.change;
    },
    clearPassword(){
      this.password = '';
    },
    LoginComfirm(){
      let _this = this;
      if(_this.phone != '' && _this.phone.length == 11){
        if(_this.password != '' && _this.password.length >= 6){
          _this.$loading('登录中...');
          _this.$http('user/login',{"account": _this.phone,"password": _this.password}).then( res => {
            _this.$loading.close();
            if(res.data.code == 1){
              let dataInfo = res.data.data;
              _this.$router.push('/');
              _this.$toast.center('登录成功');
              _this.$cookie.set("phone",dataInfo.mobile);
              _this.$cookie.set("avatar",dataInfo.avatar_domain);
              _this.$cookie.set("nickname",dataInfo.nickname);
              _this.$cookie.set("isvip",dataInfo.is_vip);
              _this.$cookie.set("token",dataInfo.token);
              _this.$cookie.set("defaultSpeed",dataInfo.speed);
              _this.$cookie.set("defaultVolume",dataInfo.volume);
            }else{
              _this.$toast.center(res.data.msg);
            }
          }).catch( err => {
            console.log(err);
          });
        }else{
          _this.$toast.center('请输入不少于6位数的密码');
        }
      }else{
        _this.$toast.center('请输入正确的手机号码');
      }
    }
  }
}
</script>

<style scoped>
.login-reg-item .icon-clear{
    position: absolute;
    top: 0;
    right: 30px;
    width: 30px;
    height: 50px;
    background: url(../assets/images/clear_icon.png) 9px 19px no-repeat;
    background-size: 12px 12px;
}
.login-reg-item .icon-change{
    position: absolute;
    top: 0;
    right: 0;
    width: 30px;
    height: 50px;
    background: url(../assets/images/login-register-pw-hide.png) 5px 19px no-repeat;
    background-size: 20px 12px;
}
.login-reg-item .icon-change.icon2{
    background: url(../assets/images/login-register-pw-show.png) 5px 19px no-repeat;
    background-size: 20px 12px;
}
</style>



