<template>
    <div class="liui-page">
      <header class="liui-bar liui-pos-f">
        <div class="icon iconfont icon-fanhuitubiao" @click="gotoHome"></div>
        <h1 class="title">合成记录</h1>
      </header>
      <section class="liui-content">
        <div class="record-list" v-if="recordList.length != 0">
          <div class="notices"><i class="iconfont icon-laba"></i>亲~您的合成音频只能保留24小时哟！</div>
          <div class="item" v-for="(item,index) in recordList">
            <div class="name txt-ellipsis">{{item.text}}<p>语速：{{item.speed}}&nbsp;&nbsp;&nbsp;发音：{{item.compound_cat_name}}&nbsp;&nbsp;&nbsp;音量：{{item.volume}}</p></div>
            <div class="icon" :class="{ icon2: index == currPlayIndex }" @click="playAudio(item,index)"></div>
            <div class="download" @click="download(item)" v-if=" isvip != 0 ">点击下载</div>
          </div>
        </div>
        <div class="no-record-data" v-else>
          <img src="../../assets/images/no-record.png">
          <p class="txt-c">这里空荡荡的~<br/>快去试一试语音合成吧</p>
        </div>
      </section>
      <audio ref="player" :src="playSrc" @ended="ended()"></audio>
    </div>
</template>

<script>
export default {
  data () {
    return {
      isvip: '',
      playSrc: '',
      currPlayIndex: -1,
      recordList: []
    }
  },
  created(){
    let phone = this.$cookie.get("phone");
    if(phone){
      this.nickname = this.$cookie.get("nickname");
      this.avatar = this.$cookie.get("avatar");
      this.isvip = parseInt(this.$cookie.get("isvip"));
      this.$loading('数据加载中...');
      this.$http('Compound/getRecord',{"page": '1',"pagesize":"99"}).then( res => {
        if(res.data && res.data.code == 1){
          this.$loading.close();
          this.recordList = res.data.data.data;
          for(let i=0;i<this.recordList.length;i++){
            this.recordList[i].isPlaying = false;
          }
        }else{
          this.$loading.close();
          this.$toast.center('登陆过期，请重新登录');
        }
      }).catch( err => {
        console.log(err);
      });
    }else{
      this.$router.push('/login');
    }
  },
  methods:{
    gotoHome(){
      this.$router.push('/user');
    },
    download(item){
      if(!item){
        return;
      }
      let isWeiXin = () => { return navigator.userAgent.toLowerCase().indexOf('micromessenger') !== -1 }
      if (isWeiXin()) {
        this.$toast.center('请使用浏览器登录下载');
      } else {
        let url = item.src;
        let link = document.createElement('a');
        link.style.display = 'none';
        link.href = url;
        link.setAttribute('download', item.text+'.mp3');
        link.setAttribute('target', '_blank');
        document.body.appendChild(link);
        link.click();
      }
    },
    ended(){
      this.currPlayIndex = -1;
      console.log("---------------------------播放完毕了");
    },
    playAudio(item,index){
      let player = this.$refs.player;
      //item.isPlaying = !item.isPlaying;
      player.src = item.src;
      this.currPlayIndex = index;
      player.play();
    }
  }
}
</script>

<style scoped>
.no-record-data{
  padding: 7rem 2rem 5rem 2rem;
}
.no-record-data > img{
  width: 120px;
  margin: 0 auto;
}
.no-record-data p{
  padding-top: 1.5rem;
  font-size: 12px;
  color: #999;
}

.record-list .notices{
  display: flex;
  padding: 0 1rem;
  height: 42px;
  line-height: 42px;
  font-size: 14px;
  background-color: #fbe9c9;
  color: #c69f57;
  align-items: center;
}
.record-list .notices i{
  margin-right: 5px;
}
.record-list .item{
  display: flex;
  padding: 0.8rem 1rem;
  line-height: 24px;
  border-bottom: 1px solid #eee;
  align-items: center;
}
.record-list .item .name{
  flex: 1;
}
.record-list .item .name p{
  color: #999;
  font-size: 10px;
}
.record-list .item .icon{
  width: 24px;
  height: 24px;
  margin: 0 1rem 0 2rem;
  background: url(../../assets/images/player-stop-icon.png) 0 0 no-repeat;
  background-size: 24px 48px;
}
.record-list .item .icon.icon2{
  background: url(../../assets/images/player-stop-icon.png) 0 -24px no-repeat;
  background-size: 24px 48px;
}
.record-list .item .download{
  color: #999;
  font-size: 12px;
}
</style>



